import {NRInsightsBackend, Core, Log} from 'newrelic-video-core';
import MagnetoHtml5Tracker from './magnetoHtml5Tracker';

export default class NewRelicAdapter {
    constructor(videoElement, getAssetArgs, asset) {
        const backend = new NRInsightsBackend('2115113', 'NRII-xiMD2VbD5SyajC7DPPRKsnGUe82JBjFg');

        Core.setBackend(backend);
        if (window.isConsoleEnabled) {
            Log.level = Log.Levels.ALL;
        }

        this.myTracker = new MagnetoHtml5Tracker(videoElement);
        this.myTracker.playSessionCustomData = {
            appName: getAssetArgs.deviceInfo?.appName,
            appVersion: getAssetArgs.deviceInfo?.appVersion,

            userId: asset.playResponse.youbora?.['user.name'] || '',

            playerName: getAssetArgs.deviceInfo?.playerName,
            playerVersion: getAssetArgs.deviceInfo?.playerVersion,

            uuid: getAssetArgs.deviceInfo?.uuid,

            osName: getAssetArgs.deviceInfo?.glassConfigure.osName,
            osVersion: getAssetArgs.deviceInfo?.glassConfigure.osVersion,

            skyAccountId: getAssetArgs.deviceInfo?.glassConfigure.accountId,
            xboServiceAccountId: getAssetArgs.deviceInfo?.glassConfigure.xboServiceAccountId,

            deviceBrand: getAssetArgs.deviceInfo?.glassConfigure.brand,
            deviceCategory: getAssetArgs.deviceInfo?.glassConfigure.category,
            deviceCountryCode: getAssetArgs.deviceInfo?.glassConfigure.countryCode,
            deviceEstbMacAddress: getAssetArgs.deviceInfo?.glassConfigure.estbMacAddress,
            deviceManufacturer: getAssetArgs.deviceInfo?.glassConfigure.manufacturer,
            deviceModel: getAssetArgs.deviceInfo?.glassConfigure.model,
            devicePartnerId: getAssetArgs.deviceInfo?.glassConfigure.partnerId,
            deviceType: (getAssetArgs.deviceInfo?.glassConfigure.brand === 'SOIP-PCK') ? 'stb' : 'tv',

            locator: getAssetArgs.glassAssetLocator,
            assetType: getAssetArgs.glassContentOptions?.assetType,

            connectionType: getAssetArgs.glassContentOptions?.connectionType,
            networkConnectionType: getAssetArgs.glassContentOptions?.connectionType,

            playServiceSessionId: asset.playResponse.sessionId,

            contentId: asset.playResponse.youbora?.['content.id'] || asset.playResponse.playerEventRequest?.body?.progress?.assetId,
            contentTitle: asset.playResponse.youbora?.['content.title'] || asset.playResponse.playerEventRequest?.body?.progress?.title,
        };
        Core.addTracker(this.myTracker);

        // Send a sample event
        // myTracker.send('SAMPLE_ACTION', {num: 1});

        this.videoElement = videoElement;
    }

    teardown() {
        Core.getTrackers().forEach((tracker) => {
            Core.removeTracker(tracker);
        });
        this.videoElement = null;
    }
}
