import IntegratedPlayerError from '../../../utils/integrated-player-error';

import {addProxyIp} from '../../../utils/utils';
import {getFileName, getCmsId} from './utils';
import {PLAY_URL} from './configs';

export default async function getMagnetoAsset({
    authToken: glassAuthToken,
    glassAssetLocator,
    glassContentOptions,

    config,
    deviceInfo,
    user,
}) {
    const fileName = getFileName(glassAssetLocator.pathname);

    const cmsId = getCmsId(glassContentOptions.assetId);

    let streamotionToken;

    user.setGlassToken(glassAuthToken); // also known as XSCT

    try {
        [streamotionToken] = await user.getStreamotionToken(20);
    } catch (error) {
        console.error(`could not fetch token ${JSON.stringify(error)}`, error);
        let message = '';

        for (const key of ['status', 'statusText', 'type']) {
            if (typeof error[key] !== 'undefined') {
                message += ` ${error[key]}`;
            }
        }

        throw new IntegratedPlayerError(
            {major: 20000, minor: 101, desc: `token service failed to return${message}`},
            {
                response: error,
                responseString: `${error}`,
            }
        );
    }

    return await fetchStreamotionPlay(
        (glassContentOptions.assetType === 'IVOD') ? '' : cmsId,
        fileName,
        glassContentOptions.mediaGuid,
        streamotionToken,
        deviceInfo,
        glassContentOptions.resumePosition,
        glassAssetLocator.searchParams
    )
        .then((playResponse) => {
            if (playResponse.streams.length < 1) {
                throw 'no streams available';
            }

            // uncomment to test fallback
            // playResponse.streams[0]?.manifest = 'http://httpstat.us/403?sleep=500';
            // playResponse.streams[1]?.manifest = 'http://httpstat.us/404?sleep=500';
            // playResponse.streams[2]?.manifest = 'http://httpstat.us/500?sleep=500';

            // playResponse.streams[0]?.licenseAcquisitionUrl['com.microsoft.playready'] = 'http://httpstat.us/404?sleep=1000';

            // const streams = [{
            //     url: 'https://rdmedia.bbc.co.uk/testcard/vod/manifests/avc-full.mpd',
            // }];

            const getRequestHeadersFunction = async function () {
                try {
                    [streamotionToken] = await user.getStreamotionToken(20);
                    return [
                        {
                            key: 'Authorization',
                            value: `Bearer ${streamotionToken}`,
                            isLicenseRequest: true,
                        },
                    ];
                } catch (error) {
                    console.error(`could not fetch token for DRM Authorization header ${JSON.stringify(error)}`, error);
                }
                return null;
            };

            const streams = playResponse.streams.map(({drm, manifest, licenseAcquisitionUrl, adTrackingUrl}) => {
                const streamUrl = new URL(manifest);

                let drmConfig = {};

                if (drm) {
                    drmConfig = {
                        'com.microsoft.playready': licenseAcquisitionUrl['com.microsoft.playready'],
                        'preferredKeysystem': 'com.microsoft.playready',
                    };
                }

                // currently adTrackingUrl is only relevant to VOD assets that expect scrubbing/trickplay to be disabled during an ad segment
                // if true, ad cannot be skipped by the user, adTrackingUrl must return before playback can play the first frame
                // if false, adTrackingUrl should be ignored
                const isFetchAdTrackingUrlRequired = adTrackingUrl
                && glassContentOptions.assetType === 'VOD';
                // && (playResponse.configurations?.isScrubbingEnabledForAds === false);

                return {
                    url: `${streamUrl}`,
                    drmConfig,
                    requestHeadersFunction: (drm) ? getRequestHeadersFunction : null,
                    autoPlay: !isFetchAdTrackingUrlRequired, // if false, do not start as soon as possible, wait for adTrackingResponse before playing the first frame
                };
            });

            return {
                streams,
                playResponse,
                aampInitConfigOverrides: {
                    enableSeekableRange: true, // enable trickplay
                    useAbsoluteTimeline: true, // enables epoch timestamp calculation
                    enableLiveLatencyCorrection: true,
                    ...config?.aampInitConfigRemoteOverrides,
                },
                // recoverySettings: config?.recoverySettings, // TODO MIPA-236 remote config for phase 2
                recoverySettings: {
                    // Phase 1: pre-stream and mid-stream fallback to different stream
                    sourceRetryCountMax: 0,
                    minimumUninterruptedPlayback: 0,

                    // // Phase 2: pre-stream and mid-stream session retry and fallback to different stream
                    // sourceRetryCountMax: 2,
                    // minimumUninterruptedPlayback: 10 * 1000,
                },
            };
        })
        .catch((error) => {
            console.error(`could not fetch streams ${JSON.stringify(error)}`, error);
            if (error?.body?.title) {
                const PLAY_NOT_ENTITLED_ERRORS = [
                    'MISSING_CONTENT_PACKAGE',
                    'UNSUFFICIENT_QUALITY_ENTITLEMENTS',
                    'DRM_ENTITLEMENTS_NOT_FOUND',
                ];

                if (PLAY_NOT_ENTITLED_ERRORS.includes(error.body.title)) {
                    throw new IntegratedPlayerError(
                        {major: 40, desc: `not entitled ${error.body.title}`},
                        {response: error}
                    );
                }
            }

            let message = '';

            for (const key of ['status', 'statusText', 'type']) {
                if (typeof error[key] !== 'undefined') {
                    message += ` ${error[key]}`;
                }
            }

            throw new IntegratedPlayerError(
                {major: 20000, minor: 102, desc: `play service failed to return${message}`},
                {
                    response: error,
                    responseString: `${error}`,
                }
            );
        });
}

// eslint-disable-next-line max-params
async function fetchStreamotionPlay(
    cmsId,
    foxtelAssetId,
    mediaGuid,
    streamotionToken,
    deviceInfo,
    resumePosition,
    locatorSearchParams
) {
    const {partnerId} = deviceInfo.glassConfigure;

    const preference = {
        trackKeys: true, // hardcode this, or do we have an API?
        trickPlay: true,
    };

    if (window.localStorage.getItem('FORCE_ADS')) {
        preference.forceAds = true;
    }

    const resolution = mediaGuid?.split('_')[1];

    switch (resolution) {
        case 'UHD':
            // if not entitled to 4K/UHD, then /play returns 403 UNSUFFICIENT_QUALITY_ENTITLEMENTS
            preference.restrictToUHD = true;
            break;

        case 'HD':
            // /play returns 1080p or lower
            preference.resolution = 'fhd';
            break;

        case 'SD':
            // /play returns 540p or lower
            preference.resolution = 'sd';
            break;

        default:
            break;
    }

    const body = {
        assetId: foxtelAssetId,
        cmsId,
        foxtelAssetId,
        ads: {
            optOut: true,
        },
        application: {
            appId: deviceInfo.appName,
            name: 'magneto',
            version: deviceInfo.appVersion,
        },
        // browser: {
        //     name: 'TODO',
        //     version: 'TODO',
        // },
        capabilities: {
            codecs: [
                'avc', // hardcode this, or do we have an API?
                'hevc', // hardcode this, or do we have an API?
            ],
            drm: [
                'playready',
            ],
        },
        device: {
            brand: deviceInfo.glassConfigure.manufacturer,
            id: deviceInfo.glassConfigure.estbMacAddress,
            model: deviceInfo.glassConfigure.model,
            type: (deviceInfo.glassConfigure.brand === 'SOIP-PCK') ? 'stb' : 'tv',
        },
        os: {
            name: 'Magneto',
            version: deviceInfo.glassConfigure.osVersion,
        },
        player: {
            name: deviceInfo.playerName,
            version: deviceInfo.playerVersion,
        },
        preference,
        session: {
            startPos: resumePosition ? resumePosition / 1000 : null, // does API accept seconds or milliseconds?
        },
    };

    if (locatorSearchParams) {
        ['start', 'end'].forEach((key) => {
            if (locatorSearchParams.has(key)) {
                body.session[key] = locatorSearchParams.get(key).replace(' ', '+');
            }
        });
    }

    return fetch(
        addProxyIp(PLAY_URL[partnerId]),
        {
            method: 'POST',
            headers: {
                'Authorization': `Bearer ${streamotionToken}`,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(body),
        }
    ).then(async (response) => {
        let responseBody;

        if (response.headers.get('Content-Type')?.indexOf('json') > -1) {
            responseBody = await response.json();
        } else {
            responseBody = await response.text();
        }

        if (response.ok) {
            return {
                requestUrl: response.url,
                responseBody,
            };
        } else if (response instanceof Response) {
            throw {
                url: response.url,
                status: response.status,
                statusText: response.statusText,
                type: response.type,
                headers: Object.fromEntries(response.headers.entries()),
                body: responseBody,
            };
        } else {
            throw `unknown error fetching streams ${response}`;
        }
    })
        .then(({responseBody, requestUrl}) => {
            console.debug(`${requestUrl} response ${JSON.stringify(responseBody)}`); // eslint-disable-line no-console
            return responseBody;
        });
}
