// based on https://github.com/newrelic/video-html5-js/blob/master/src/tracker.js

import {VideoTracker} from 'newrelic-video-core';
// import {VideoTracker, Log} from 'newrelic-video-core';

export default class MagnetoHtml5Tracker extends VideoTracker {
    registerListeners() {
        this.listeners = {
            loadstart: this.onDownload.bind(this),
            loadedmetadata: this.onDownload.bind(this),
            loadeddata: this.onDownload.bind(this),
            canplay: this.onDownload.bind(this),
            play: this.onPlay.bind(this),
            playing: this.onPlaying.bind(this),
            pause: this.onPause.bind(this),
            seeking: this.onSeeking.bind(this),
            seeked: this.onSeeked.bind(this),
            error: this.onError.bind(this),
            ended: this.onEnded.bind(this),
            waiting: this.onWaiting.bind(this),

            anomalyReport: this.onAnomalyReport.bind(this),
            bitrateChanged: this.onBitrateChanged.bind(this),
            blocked: this.onBlocked.bind(this),
            drmMetadata: this.onDrmMetadata.bind(this),
            timeupdate: this.onTimeupdate.bind(this),
            tuneMetricsData: this.onTuneMetricsData.bind(this),
        };

        for (const key in this.listeners) {
            this.player.addEventListener(key, this.listeners[key]);
        }
        // Log.debugCommonVideoEvents(this.player); // enable for local only, do not commit, otherwise duplicate logs will appear
    }

    unregisterListeners() {
        for (const key in this.listeners) {
            this.player.removeEventListener(key, this.listeners[key]);
        }
    }

    getCdn() {
        return null;
    }

    getDuration() {
        return this.player?.duration * 1000;
    }

    getFps() {
        return this.player?.videoFramerate;
    }

    getLanguage() {
        return null;
    }

    getPlayhead() {
        return this.player?.currentTime * 1000;
    }

    getPlayerName() {
        return null;
    }

    getPlayerVersion() {
        return null;
    }

    getPlayrate() {
        return this.player?.playbackRate;
    }

    getPreload() {
        return null;
    }

    getBitrate() {
        return null;
    }

    getRenditionName() {
        return null;
    }

    getRenditionBitrate() {
        return this.player?.videoBitrate;
    }

    getRenditionHeight() {
        return this.player?.videoHeight;
    }

    getRenditionWidth() {
        return this.player?.videoWidth;
    }

    getSrc() {
        return this.player?.currentSrc;
    }

    getTitle() {
        return null;
    }

    getTrackerName() {
        return 'magnetoHtml5';
    }

    // currently set by newrelic-video-core
    // getTrackerVersion() {
    //     return '0.1.1';
    // }

    getVideoId() {
        return null;
    }

    isAutoplayed() {
        return null;
    }

    isFullscreen() {
        return null;
    }

    isLive() {
        return null;
    }

    isMuted() {
        return null;
    }

    onBlocked({detail}) {
        if (this.isVideoSessionActive) {
            if (detail?.blocked !== this.blocked) { // if blocked has changed
                const eventName = (detail?.blocked) ? 'PARENTAL_CONTROL_BLOCK' : 'PARENTAL_CONTROL_UNBLOCK';

                this.send(eventName, addPrefixToAllKeys(detail || {}, 'parentalControl.'));
            }

            this.blocked = detail?.blocked;
        }
    }

    onBitrateChanged() {
        this.sendRenditionChanged();
    }

    onDownload(e) {
        this.sendDownload({state: e.type});
    }

    onPlay(payload) {
        this.isVideoSessionActive = true;

        // all values in customData must be string or integer, they cannot be more objects.
        const customData = {
            ...addPrefixToAllKeys(payload?.detail?.recovery?.state, 'recovery.'),
            ...addPrefixToAllKeys(payload?.detail?.recovery?.settings, 'recovery.'),
            ...this.playSessionCustomData,
        };

        // setting customData overrides all previous customData
        this.setOptions({
            customData,
        });

        this.sendRequest();
    }

    onPlaying() {
        // first frame is playing
        const timeFromSetAssetToFirstFrame = (this.player?.perceivedVideoStartBeginEpoch)
            ? new Date().getTime() - this.player.perceivedVideoStartBeginEpoch
            : null;

        this.player.perceivedVideoStartBeginEpoch = null;

        this.sendBufferEnd();
        this.sendResume();
        this.sendStart({
            timeFromSetAssetToFirstFrame,
        });
    }

    onPause() {
        this.sendPause();
    }

    onSeeking() {
        this.sendSeekStart();
    }

    onSeeked() {
        this.sendSeekEnd();
    }

    onTimeupdate() {
        // timeupdate is called every 500ms so these fields are updated very often
        // any and every event fired will have the latest value, time measurements are accurate to +/- 500ms
        if (this.customData) {
            this.customData.contentPlayheadBuffer = this.player?.bufferedSeconds * 1000 || null;

            if (this.player?.liveLatency > 0) {
                this.customData.contentPlayheadLiveLatency = this.player.liveLatency * 1000;
            }

            if (this.player?.epochOffsetSeconds > 0) {
                this.customData.contentPlayheadEpochOffset = this.player.epochOffsetSeconds * 1000;
            }

            this.customData.networkBandwidth = this.player?.networkBandwidth;
        }
    }

    onError(payload) {
        const {code: errorCode, description: errorDescription, ...rest} = payload.detail;
        const errorFields = addPrefixToAllKeys(rest, 'errorField.');

        this.sendError({
            errorCode,
            errorDescription,
            ...errorFields,
        });
    }

    onEnded() {
        this.isVideoSessionActive = false;
        this.sendEnd();
        this._lastRendition = null; // workaround, reset _lastRendition between sessions
        if (this.state) {
            this.state.initialBufferingHappened = false; // workaround, reset bufferType and isInitialBuffering between sessions
        }

        // this.state.timeSinceLastRenditionChange = null; // results in an error
    }

    onWaiting() {
        this.sendBufferStart();
    }

    onAnomalyReport({detail: {anomalyReport}}) {
        if (/^(Rate changed|Tune attempt)/.test(anomalyReport?.description)) {
            // ignore event
            return false;
        }

        this.send('AAMP_ANOMALY_REPORT', addPrefixToAllKeys(anomalyReport || {}, 'anomalyReport.'));
    }

    onDrmMetadata({detail: {payload}}) {
        payload.headersString = JSON.stringify(payload.headers);
        const networkMetricsObject = JSON.parse(payload.networkMetrics || '{}');

        delete payload.headers;
        delete payload.networkMetrics;
        delete payload.sessionId;

        Object.assign(payload, addPrefixToAllKeys(networkMetricsObject || {}, 'networkMetrics.'));
        this.send('AAMP_DRM_METADATA', addPrefixToAllKeys(payload || {}, 'drmMetadata.'));
    }

    onTuneMetricsData({detail: {tuneMetricsData}}) {
        this.send('AAMP_TUNE_METRICS_DATA', addPrefixToAllKeys(tuneMetricsData || {}, 'tuneMetricsData.'));
    }
}

function addPrefixToAllKeys(object, prefix) {
    return Object.fromEntries(
        Object.entries(object).map(([k, v]) => [`${prefix}${k}`, v])
    );
}
