/* eslint-disable no-console */
import jwtDecode from 'jwt-decode';
import {addProxyIp} from '../../../utils/utils';
import sendNewRelicLog from '../../../utils/new-relic-log';
import {getExpiresInMs} from './utils';
import {AUTH_CONFIG} from './configs';

export default class User {
    authConfig = {};

    storedGlassToken = null;
    storedStreamotionToken = null;
    storedScope = null;

    constructor(partnerId, deviceBrand) {
        this.partnerId = partnerId;
        this.deviceBrand = deviceBrand;
    }

    async setGlassToken(newGlassToken, reuseExistingGlassToken = false) {
        // also known as XSCT
        const currentGlassToken = reuseExistingGlassToken ? this.getGlassToken() : false;

        if (newGlassToken === currentGlassToken) {
            console.log('glassToken matches existing');
        } else {
            console.log('glassToken is new');
            this.storeGlassToken(newGlassToken);
            this.deleteStreamotionToken();
        }
    }

    storeGlassToken(glassToken) {
        this.storedGlassToken = glassToken;
    }

    getGlassToken() {
        if (!this.storedGlassToken) {
            return undefined;
        }

        return this.storedGlassToken;
    }

    async fetchStreamotionToken(previousToken) {
        const configName = `${this.partnerId}-${(this.deviceBrand === 'SOIP-PCK') ? 'puck' : 'tv'}`;
        const authConfig = AUTH_CONFIG[configName];
        const headers = {
            'Content-Type': 'application/json',
        };

        if (previousToken) {
            // MIPA-178 if previous or expired streamotionToken exists then attach it as bearer token
            headers.Authorization = `Bearer ${previousToken}`;
        }

        return fetch(
            addProxyIp(`${authConfig.domain}/v2/xsct/oauth/token`),
            {
                method: 'POST',
                headers,
                body: JSON.stringify({
                    ...authConfig.body,
                    token: this.getGlassToken(),
                }),
            }
        ).then(async (response) => {
            let responseBody;

            if (response.headers.get('Content-Type')?.indexOf('json') > -1) {
                responseBody = await response.json();
            } else {
                responseBody = await response.text();
            }

            if (response.ok) {
                return responseBody;
            } else if (response instanceof Response) {
                throw {
                    url: response.url,
                    status: response.status,
                    statusText: response.statusText,
                    type: response.type,
                    headers: Object.fromEntries(response.headers.entries()),
                    body: responseBody,
                };
            } else {
                throw `unknown error fetching token ${response}`;
            }
        });
    }

    async getStreamotionToken(expiryBufferSeconds = 20) {
        if (this.storedStreamotionToken) {
            const payload = jwtDecode(this.storedStreamotionToken);

            if (getExpiresInMs(payload) > expiryBufferSeconds * 1000) {
                return [this.storedStreamotionToken, payload];
            }
        }

        return await this.fetchStreamotionToken(localStorage.getItem('au.com.foxtel_refreshToken'))
            .then((body) => {
                this.storeStreamotionToken(body);
                this.videoElement?.addRequestHeaders([
                    {
                        key: 'Authorization',
                        value: `Bearer ${this.storedStreamotionToken}`,
                        isLicenseRequest: true,
                    },
                ]);

                const payload = jwtDecode(this.storedStreamotionToken);

                getExpiresInMs(payload);

                return [this.storedStreamotionToken, payload];
            });
    }

    storeStreamotionToken({access_token: accessToken, scope}) {
        this.storedStreamotionToken = accessToken;
        try {
            localStorage.setItem('au.com.foxtel_refreshToken', accessToken);
        } catch (error) {
            const localStorageCharacterLength = {};

            for (const key of Object.keys(localStorage)) {
                localStorageCharacterLength[key] = localStorage[key].length;
            }
            console.warn('could not stash refreshToken');
            sendNewRelicLog('could not stash refreshToken, /token will hit XMP', {
                level: 'WARN',
                response: error,
                responseString: `${error}`,
                localStorageCharacterLength,
            });
        }

        this.storedScope = scope;
    }

    deleteStreamotionToken() {
        this.storedStreamotionToken = undefined;
        this.storedScope = undefined;
    }

    deleteStoredTokens() {
        this.storedGlassToken = undefined;
        this.deleteStreamotionToken();
    }

    async logoutUser() {
        this.deleteStoredTokens();
    }

    setVideoElement(videoElement) {
        this.videoElement = videoElement;
    }
}
