import {OzTAMService} from './OztamJSPlugin';

export const MEDIA_TYPES = {
    live:  'live',
    vod: 'vod',
};

export default class OztamAdapter {
    constructor(videoElement, getAssetArgs, asset, isProduction = false) {
        if (!(asset.playResponse.oztam?.publisherId)) {
            // Oztam not required
            return;
        }

        this.videoElement = videoElement;
        this.asset = asset;
        this.getAssetArgs = getAssetArgs;

        this.blocked = false;

        this.mediaType = (asset.playResponse.oztam.reportType === 'vod') ? MEDIA_TYPES.vod : MEDIA_TYPES.live;
        if (asset.playResponse.oztam?.transmissionTime) {
            this.transmissionTimeEpochSeconds = new Date(asset.playResponse.oztam.transmissionTime).getTime() / 1000;
        }

        this.listeners = {
            playing: this.onPlaying.bind(this),
            seeked: this.onSeeked.bind(this),
            ended: this.onEnded.bind(this),
            pause: this.onPause.bind(this),
            blocked: this.onBlocked.bind(this),
        };
        this.addListeners();

        // MIPA-114 workaround, solution to be provided by /play response
        const vendorVersionDeviceName = (getAssetArgs.deviceInfo?.glassConfigure.brand === 'SOIP-PCK') ? 'puck' : 'tv';

        this.oztamService = new OzTAMService(
            asset.playResponse.oztam.publisherId,
            `kayo_foxtel-${vendorVersionDeviceName}_${getAssetArgs.deviceInfo?.appVersion}`,
            isProduction,
            window.isConsoleEnabled // isVerboseLogging
        );

        const isLiveReportType = this.mediaType === MEDIA_TYPES.live;

        this.getCurrentTime = (currentTime = videoElement.currentTime) => {
            let time = 0;

            if (isLiveReportType) {
                const offset = (videoElement.epochOffsetSeconds) ? videoElement.epochOffsetSeconds : this.transmissionTimeEpochSeconds;

                time = offset + currentTime;
            } else {
                time = currentTime;
            }

            return time;
        };
        this.getCurrentTime.bind(this);
    }

    addListeners = () => {
        for (const key in this.listeners) {
            this.videoElement.addEventListener(key, this.listeners[key]);
        }
    };

    removeEventListeners = () => {
        for (const key in this.listeners) {
            this.videoElement?.removeEventListener(key, this.listeners[key]);
        }
    };

    onPlaying = (event) => {
        clearTimeout(this.inactivityTimeoutId);

        if (this.blocked === true && this.hasSessionStarted === false) {
            // ignore this event, video session hasn't started yet
            return false;
        }

        if (event.detail?.isFirstPlaying) {
            // video start successful
            if (this.oztamService) {
                this.start();
            }
        } else if (this.hasSessionStarted === false) {
            // create new session after inactivityTimeout killed the previous one
            this.startSession();
        } else {
            // resume
            this.oztamService?.resumeProgress();
        }
    };

    onBlocked = ({detail}) => {
        if (detail.blocked === this.blocked) {
            // state hasn't changed
            return;
        } else {
            // state has changed, stash state
            this.blocked = detail.blocked;
        }

        if (detail.blocked) {
            // video/audio cannot be seen/heard, ignore all events
            this.endSession();
        } else {
            // video can be seen, audio can be heard
            this.startSession();
        }
    }

    onSeeked = () => {
        this.oztamService?.seekBegin();
        this.oztamService?.seekComplete();
    };

    onEnded = () => {
        if (this.mediaType === 'vod' && this.videoElement.duration && this.videoElement.currentTime >= this.videoElement.duration - 1) {
            // finished, final segment in manifest has played
            this.oztamService?.complete();
        } else {
            // user exit
            this.oztamService?.stop();
        }
    };

    onPause = () => {
        this.oztamService?.haltProgress();

        clearTimeout(this.inactivityTimeoutId);
        this.inactivityTimeoutId = setTimeout(this.endSession, 10 * 60 * 1000);
    };

    start = () => {
        const oztamObject = this.asset.playResponse.oztam;
        const mediaId = oztamObject.assetId;
        const {duration, currentSrc: url} = this.videoElement;
        const {mediaType} = this;

        this.oztamService.forceDeviceType('tv');
        this.oztamService.startSession(
            mediaId,
            url,
            duration,
            mediaType
        );

        const properties = {
            channel: oztamObject.channel,
            connectionType: (this.getAssetArgs.glassContentOptions?.connectionType === 'ETHERNET') ? 'LAN' : 'WIFI',
            demo1: oztamObject.demo1,
            deviceId: this.getAssetArgs.deviceInfo?.uuid, // must match the device UUID that we share with third party providers
            seriesId: oztamObject.seriesId,
            seriesName: oztamObject.seriesName,
        };

        this.oztamService.beginPlayback(
            mediaId,
            url,
            duration,
            this.getCurrentTime,
            properties,
            mediaType,
            this.getCurrentTime()
        );
    }

    startSession = () => {
        this.hasSessionStarted = true;
        this.onPlaying({detail: {
            isFirstPlaying: true,
        }});
    }

    endSession = () => {
        this.onEnded();
        this.hasSessionStarted = false;
    }

    teardown() {
        this.endSession();
        clearTimeout(this.inactivityTimeoutId);
        this.removeEventListeners();
        this.videoElement = null;
    }
}
