/* eslint-disable no-console */

import {addProxyIp} from '../../utils/utils';
import NewRelicAdapter from './utils/analytics/new-relic/new-relic';
import OztamAdapter from './utils/analytics/oztam/oztam';
import VimondPlayerEventsAdapter from './utils/analytics/vimond-player-events';
// import YouboraAdapter from './utils/analytics/youbora/youbora6';
import YouboraAdapter from './utils/analytics/youbora/youbora7';
import User from './utils/user';
import getMagnetoAsset from './utils/get-asset';
import {fetchWithTimeoutAndRetry, getVideoAdBreaks} from './utils/utils';

export default class MagnetoProvider {
    constructor(config) {
        this.config = config;
        this.user = null;
        this.asset = [];
    }

    /**
     * @typedef {import('../../app/get-asset').GetAssetRequest} GetAssetRequest
     * @typedef {import('../../app/get-asset').GetAssetResponse} GetAssetResponse
     */
    /**
     * @param {GetAssetRequest} args requested asset and device information
     * @returns {GetAssetResponse} streams for the requested asset
     */
    async getAsset(args) {
        this.getAssetArgs = args;
        const {brand, partnerId} = args.deviceInfo.glassConfigure;

        if (this.user === null) {
            // Only create new user instance if we haven't one already.
            this.user = new User(partnerId, brand);
        }

        this.asset = await getMagnetoAsset({
            ...args,
            config: this.config,
            user: this.user,
        });
        return this.asset;
    }

    onProviderEvent(event) {
        console.log('MagnetoProvider Event', event.type, event.detail, this.videoElement?.currentTime);
    }

    monitoringSetup(videoElement) {
        this.monitoringTeardown(false);
        this.videoElement = videoElement;
        this.user.setVideoElement(videoElement);

        this.debuglisteners = (window.isConsoleEnabled) ? {
            // html5 video events
            ended:          this.onProviderEvent.bind(this),
            error:          this.onProviderEvent.bind(this),
            loadedmetadata: this.onProviderEvent.bind(this),
            pause:          this.onProviderEvent.bind(this),
            play:           this.onProviderEvent.bind(this),
            playing:        this.onProviderEvent.bind(this),
            seeked:         this.onProviderEvent.bind(this),
            seeking:        this.onProviderEvent.bind(this),
            timeupdate:     this.onProviderEvent.bind(this),
            waiting:        this.onProviderEvent.bind(this),

            // AAMP events
            bitrateChanged: this.onProviderEvent.bind(this),
            id3Metadata:    this.onProviderEvent.bind(this),
        } : {};
        this.listeners = {
            play:           this.onPlay.bind(this),
            loadedmetadata: this.onLoadedMetadata.bind(this),
            ended:          this.onEnded.bind(this),
            tracksChanged:  this.onTracksChanged.bind(this),
        };

        for (const key in this.listeners) {
            this.videoElement.addEventListener(key, this.listeners[key]);
        }
        for (const key in this.debuglisteners) {
            this.videoElement.addEventListener(key, this.debuglisteners[key]);
        }

        const getAssetArgs = this.getAssetArgs || {};

        this.newRelicAdapter = new NewRelicAdapter(videoElement, getAssetArgs, this.asset);

        if (this.config?.oztam?.isEnabled || window.localStorage.getItem('OZTAM_ENABLED') === 'true' || false) {
            this.oztamAdapter = new OztamAdapter(videoElement, getAssetArgs, this.asset, this.config?.oztam?.isProduction);
        }

        const isVimondPlayerEventsEnabled = true;

        if (isVimondPlayerEventsEnabled) {
            this.vimondAdapter = new VimondPlayerEventsAdapter(videoElement, getAssetArgs, this.asset, this.user);
        }

        this.youboraAdapter = new YouboraAdapter(videoElement, getAssetArgs, this.asset);
    }

    monitoringTeardown(resetAsset = true) {
        // Detach your listeners, send your final heartbeats, etc.

        if (this.videoElement) {
            for (const key in this.listeners) {
                this.videoElement?.removeEventListener(key, this.listeners[key]);
            }
            for (const key in this.debuglisteners) {
                this.videoElement?.removeEventListener(key, this.debuglisteners[key]);
            }
        }

        this.newRelicAdapter?.teardown();
        this.newRelicAdapter = null;

        this.oztamAdapter?.teardown();
        this.oztamAdapter = null;

        this.vimondAdapter?.teardown();
        this.vimondAdapter = null;

        this.youboraAdapter?.teardown();
        this.youboraAdapter = null;

        this.videoElement = null;

        // reset stashed variables
        if (resetAsset) {
            this.asset = null;
            this.getAssetArgs = null;
        }
    }

    onPlay(payload) {
        this.currentStream = payload.detail.stream;

        const [currentPlayStream, ...overflow] = this.asset.playResponse.streams.filter(
            (stream) => stream && (this.videoElement?.currentSrc?.indexOf(stream.manifest) > -1)
        );

        if (!currentPlayStream) {
            console.error('currentPlayStream not found', currentPlayStream);
            return;
        } else if (overflow.length > 0) {
            console.error('more than one currentPlayStream found', overflow);
            return;
        }

        console.debug(`currentPlayStream=${JSON.stringify(currentPlayStream)}`); // eslint-disable-line no-console
        this.currentPlayStream = currentPlayStream;
        if (this.vimondAdapter) {
            this.vimondAdapter.playStream = currentPlayStream;
        }

        if (this.youboraAdapter) {
            this.youboraAdapter.playStream = currentPlayStream;
        }
    }

    onEnded() {
        this.currentPlayStream = null;
        this.videoAdBreaks = null;
    }

    onTracksChanged(payload) {
        if (payload.detail.tracks) {
            this.youboraAdapter.tracks = payload.detail.tracks;
        }
    }

    async onLoadedMetadata() {
        if (this.currentStream.autoPlay) {
            console.info(`autoplaying, no need to hit adTrackingUrl ${this.currentPlayStream?.adTrackingUrl}`);
            this.setVideoAdBreaksCallback([]);
            return;
        }

        const url = addProxyIp(this.currentPlayStream.adTrackingUrl);

        console.info(`sending request to adTrackingUrl ${url}`);
        this.videoAdBreaks = await fetchWithTimeoutAndRetry(url, {}, 10000)
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw `Unable to get adTracking responseBody. ${url} ${response.status} ${response.statusText}`;
                }
            })
            .then((responseBody) => {
                // set MagnetoProvider.videoAdBreaks
                this.videoAdBreaks = getVideoAdBreaks(responseBody);
                // set player.videoAdBreaks
                this.setVideoAdBreaksCallback(this.videoAdBreaks);
            })
            .catch((error) => {
                console.error('adTrackingUrl failed to return', error);
                this.setVideoAdBreaksCallback(null, error);
            });
    }
}
