import {RECOVERABLE_HTTP_STATUS_CODES} from '../../../utils/constants';

export async function fetchWithTimeout(url, opts = {}, timeout = 5000) {
    // Create the AbortController instance, get AbortSignal
    // eslint-disable-next-line compat/compat
    const abortController = new AbortController();
    const {signal} = abortController;

    // Make the fetch request
    const _fetchPromise = fetch(url, {
        ...opts,
        signal,
    });

    // Start the timer
    const timer = setTimeout(() => abortController.abort(), timeout);

    // Await the fetch with a catch in case it's aborted which signals an error
    try {
        const result = await _fetchPromise;

        clearTimeout(timer);
        return result;
    } catch (e) {
        clearTimeout(timer);
        throw e;
    }
}

export async function fetchWithTimeoutAndRetry(url, opts = {}, timeout = 5000, retries = 2, delayBetweenRetries = 0) {
    const isRecoverableError = (statusCode) => statusCode in RECOVERABLE_HTTP_STATUS_CODES;

    const attemptFetch = async (retriesRemaining) => {
        // eslint-disable-next-line compat/compat
        const abortController = new AbortController();
        const {signal} = abortController;

        const _fetchPromise = fetch(url, {
            ...opts,
            signal,
        });

        const timer = setTimeout(() => abortController.abort(), timeout);

        try {
            const result = await _fetchPromise;

            clearTimeout(timer);

            if (!result.ok && isRecoverableError(result.status) && retriesRemaining > 0) {
                await new Promise((resolve) => setTimeout(resolve, delayBetweenRetries));
                return attemptFetch(retriesRemaining - 1);
            }

            return result;
        } catch (e) {
            clearTimeout(timer);
            if (retriesRemaining > 0 && (e.name === 'AbortError' || (e.response && isRecoverableError(e.response.status)))) {
                await new Promise((resolve) => setTimeout(resolve, delayBetweenRetries));
                return attemptFetch(retriesRemaining - 1);
            } else {
                throw e;
            }
        }
    };

    return attemptFetch(retries);
}

export function getVideoAdBreaks(adTrackingResponseBody) {
    const getVideoAdFromPodAd = ({adId, startTimeInSeconds, durationInSeconds}) => ({
        id: adId, // other id fields are available: vastAdId
        startTime: startTimeInSeconds * 1000, // milliseconds
        duration: durationInSeconds * 1000, // milliseconds
        watched: false,
    });

    const getVideoAdBreakFromPod = ({startTimeInSeconds, ads}) => ({
        adBreakPlacementType: (startTimeInSeconds === 0 ? 'preroll' : 'midroll'), // "preroll" | "midroll" | "postroll"
        ads: ads.map(getVideoAdFromPodAd),
        // reservationId: availId,
    });

    if (adTrackingResponseBody) {
        return adTrackingResponseBody.avails.map(getVideoAdBreakFromPod).filter(
            (videoAdBreak) => videoAdBreak?.ads?.length > 0
        );
    } else {
        return [];
    }
}

export function getExpiresInMs(payload, currentTime = new Date().getTime()) {
    let expiresIn = 0;

    if (payload?.exp) {
        expiresIn = (payload.exp * 1000) - currentTime;
    }

    console.log(`getStreamotionToken() |expiresIn=${convertMsToTime(expiresIn)}`); // eslint-disable-line no-console
    return expiresIn;
}

export function convertMsToTime(milliseconds) {
    const totalSeconds = Math.floor(milliseconds / 1000);
    const minutes = Math.floor(totalSeconds / 60);

    return `${minutes}m${totalSeconds % 60}s`;
}

export function getFileName(url) {
    if (url) {
        const lastSlashIndex = url.lastIndexOf('/');
        const lastDotIndex = url.lastIndexOf('.');

        if (lastDotIndex !== -1 && lastDotIndex > lastSlashIndex) {
            return url.substring(lastSlashIndex + 1, lastDotIndex);
        } else {
            return url.substring(lastSlashIndex + 1);
        }
    }

    return '';
}

export function getCmsId(glassAssetId) {
    if (glassAssetId) {
        const startIndex = glassAssetId.indexOf(':');
        const endIndex = glassAssetId.includes('_') ? glassAssetId.indexOf('_') : glassAssetId.length;

        if (startIndex > -1 && endIndex > -1 && endIndex > startIndex) {
            return glassAssetId.substring(startIndex + 1, endIndex).replace('-LIVOD', '');
        }
    }

    return '';
}
